.area{  
  width: 100%;
}
.staticShapes{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.staticShapes li:nth-child(1){
  position:absolute;
  left:93.5%;
  top:15%;
  opacity:0.6
}
.staticShapes li:nth-child(2){
