.shapes{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.shapes li{
  position: absolute;
  display: block;
  list-style: none;   
  animation: animate 25s linear infinite;
  bottom: 150px;  
}

.shapes li:nth-child(1){
  left: 2%;
  top:15%;
  animation-delay: 0s;
  animation-duration: 6s;
  opacity:0.3
}
